.dialogItem {
   padding: 5px;
}

.wrapper {
   display: flex;
   align-items: center;

   &__image {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 15px;
      border-radius: 50%;
      overflow: hidden;
      width: 50px;
      height: 50px;


      img {
         width: 100%;
      }
   }
}